<template>
  <div class="settings">
    <q-card class="item">
      <q-card-section>
        <h6 class="title">Check / Архив</h6>
        <div class="block">
          <p class="description">Автоматическая проверка файлов перед архивированием</p>
          <q-toggle v-model="autoCheck" color="primary" label />
        </div>
      </q-card-section>
    </q-card>
  </div>
</template>

 <script>
import { mapState, mapMutations } from 'vuex';

export default {
  name: 'Settings',
  data: function () {
    return {
      autoCheck: false,
    };
  },
  computed: {
    ...mapState({
      globalOptions: (state) => state.settings.options,
    }),
  },
  watch: {
    autoCheck(val) {
      this.globalOptions.checkArchive.autoCheck = val;
      this.setGlobalOptions();
    },
  },
  methods: {
    ...mapMutations(['setGlobalOptions']),
    getGlobalOptions() {
      this.autoCheck = this.globalOptions.checkArchive.autoCheck;
    },
  },
  mounted() {},
  created() {
    this.getGlobalOptions();
  },
};
</script>

 <style  lang="scss" scoped>
.settings {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 1rem;
}

.item {
  margin-bottom: 1rem;
}

.item .title {
  margin: 0;
  margin-bottom: 1rem;
}

.block .description {
  margin-bottom: 0.2rem;
}

.block:not(:last-child) {
  border-bottom: 1px solid rgb(197, 197, 197);
  margin-bottom: 0.5rem;
  padding-bottom: 0.5rem;
}
</style>